*
{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html{
    font-smooth:subpixel-antialiased;
    -webkit-font-smoothing: antialiased;
}
body
{
    font-smooth:subpixel-antialiased;
    -webkit-font-smoothing: antialiased;
    overflow-x: hidden;
    font-family: 'Roboto', sans-serif;
}
body h1,h2,h3,h4,h5,h6 {
    color: rgb(255, 255, 255);
    font-size: 2rem;
}
body p{
    color: white;
}
body li{
    list-style-type: none;
}

.app-wrapper{
    width: 100%;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    gap: 2em;
    padding: 1em;
    background:rgb(37, 37, 116);
}

.app-wrapper img{
    width: 150px;
    height: 150px;
}

header{
    width: 100%;
    text-align: center;
}
header p{
    color: white;
    font-size: 2.5rem;
    font-weight: 500;
    text-align: left;
    line-height: 1.5em;
}


.notes-list{
    color: white;
    width: 100%;
    overflow: scroll;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    gap: 1em;
}
.note-wrapper{
    width: 100%;
    min-height: 15vh;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: rgba(0, 0, 0, 0.224);
    padding: 1.5em;
    border-radius: 2em;
}
.note-wrapper:nth-child(2n){
    background: rgba(0, 0, 0, 0.518);
}
.note-wrapper:hover .delete-note-btn{
    opacity: 1;
}
.note-wrapper li{
    width: 70%;
    overflow: auto;
}
.note-importance-btn{
    background: transparent;
    border: none;
    color: rgb(109, 109, 109);
}
.delete-note-btn{
    opacity: 1;
    border: none;
    background: transparent;
    display: flex;
    justify-content: center;
    align-items: center;
    color: rgb(109, 109, 109);
    font-size: 1.5rem;
}