.checkbox{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 1em;
}

.checkbox p{
	color: white;
}

input[type=checkbox]{
	height: 0;
	width: 0;
	visibility: hidden;
}

label {
	cursor: pointer;
	text-indent: -9999px;
	width: 50px;
	height: 25px;
	background: rgb(255, 255, 255);
	display: block;
	border-radius: 100px;
	position: relative;
}

label:after {
	content: '';
	position: absolute;
	top: 1px;
	left: 2px;
	width: 23px;
	height: 23px;
	background: #fff;
	border-radius: 90px;
	transition: 0.1s;
}

input:checked + label {
	background: #34ff2d;
}

input:checked + label:after {
	left: calc(100% - 2px);
	transform: translateX(-100%);
}

label:active:after {
	width: 130px;
}