.form{
    position: fixed;
    bottom: 5%;
    color: white;
    border-radius: 3em;
    width: 90%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.form input{
    opacity: 0;
    background: rgba(0, 0, 0, 0.0);
    border: none;
    width: 90%;
    height: 3em;
    padding: 1em;
    color: white;
}
.form input:focus{
    outline: none;
    color: white;
}
.form input::placeholder{
    color: rgb(218, 218, 218);
    font-size: 1.1rem;
}
.submit-btn{
    background: transparent;
    border: none;
    color:#fffefe;
    font-size: 4rem;
    display: flex;
    justify-content: center;
    align-items: center;
}