.search-wrapper{
    background-color: rgba(0, 0, 0, 0.151);
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 1em;
    color: white;
    width: 100%;
    border-radius: 1em;
    padding:0.5em 1em;
}

.menu-btn{
    width: 50px;
    height: 40px;
    border-radius: 0.5em;
    background-color: rgba(0, 0, 0, 0.331);
    display: flex;
    justify-content: center;
    align-items: center;
}

.search-wrapper input{
    background: transparent;
    border: none;
    width: 80%;
    height: 100%;
}
.search-wrapper input:focus{
    outline: none;
}
.account-img-wrapper{
    background: white;
    width: 3.5em;
    height: 2.5em;
    border-radius: 50%;
    overflow: hidden;
    
}
.account-img-wrapper img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}