/* Sidebar */
.sidebar{
    position: fixed;
    background: white;
    top: 0;
    left: 0;
    z-index: 10;
    width: 80%;
    height: 100vh;
    transform: translateX(-100vw);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}


.sidebar-icons {
    font-size: 1.7rem;
    color: var(--light-color);
}


.sidebar-top{
    width: 100%;
    border-bottom: 1px solid grey;
    color: var(--light-color);
    display: flex;
    justify-content: space-between;
}
.sidebar-top h1{
    color: var(--light-color);
    padding: 1em 2em;
}
.close-sidebar-btn{
    border: none;
    background: transparent;
}


.sidebar-account{
    width: 100%;
    height: max-content;
    display: flex;
    flex-direction: column;
}
.sidebar-account p{
    color: var(--light-color);
    padding: 1em;
    font-size: 1.2rem;
}
.sidebar-account a{
    width: 100%;
    color: var(--light-color);
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 0.5em;
    padding: 1em 1em;
    font-size: 1.2rem;
}


.sidebar-buttons{
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 1em;
}
.sidebar-logout-btn, .sidebar-support-btn{
    border: none;
    width: 100%;
    color: var(--light-color);
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 0.5em;
    padding: 1em 1em;
    font-size: 1.2rem;
}