.login-wrapper{
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: white;
}
.login-wrapper p{
    color: black;
}

.login-form{
    width: 80%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 1em;
}

.login-form input{
    border: 2px solid rgb(37, 37, 116);
    border-radius: 1em;
    width: 100%;
    padding: 1em 2em;
}

.login-btn{
    border: none;
    border-radius: 1em;
    background: rgb(37, 37, 116);
    color: white;
    padding:1em 2em;
    width: 100%;
    text-align: center;
    text-decoration: none;
}

.social-login-btn{
    border: 1px solid black;
    background: transparent;
    border-radius: 1em;
    padding:1em 2em;
    width: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 1em;
    font-size: 1.1rem;
}

.sign-up-btn{
    color: rgb(37, 37, 116);
    font-weight: 500;
    font-size: 1.2rem;
}